#root {
    width: calc(100% - 40px);
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;
}

h3 {
    margin-top: 1.5rem;
}

fieldset {
    border: 1px solid #d3d7db;
    margin-top: 1.5rem;
    padding-top: 0;
}
label {
    margin-bottom: 0;
}
legend {
    display: inline;
    width: inherit;
    font-weight: 600;
    font-size: 1.75rem;
}
input {
    box-shadow: inset 0 0 0 1px hsl(0, 0%, 80%) !important;
    cursor: text;
}
input[type="color"] {
    width: 4.5rem;
    height: 2.5rem;
    padding: 0;
    cursor: pointer;
}
input[type="number"] {
    width: 100px;
}
input[type="file"] {
    display: none;
}
:not(.flex-container) > button[type="submit"] {
    padding: 1.25rem 3rem;
}

input:focus {
    border-color: #96c2f1;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}
.form-control {
    width: 100%;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.form-group {
    margin-top: 1rem;
}
.form-row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.text-muted {
    display: inline-block;
    color: #6c757d;
}

.toggle-label {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 1.25rem;
    padding-bottom: 0.75rem;
    box-shadow: inset 0 -2px 0 0 #c8c8c8;
    font-size: 1.5rem;
    font-weight: 500;
}
hr {
    background-color: #848484;
}
.help {
    margin-top: -0.5rem;
    margin-bottom: 0.5rem;
}
input[id|="react-select"] {
    box-shadow: none !important;
}
.inbl,
.u-inbl {
    vertical-align: middle;
    min-width: 100px;
}
.alert--danger {
    white-space: pre-line;
}
.btn {
    font-weight: normal;
    font-size: 1.6rem;
}

ol.sortable-container {
    padding-left: 1rem;
}
.sortable-item {
    pointer-events: auto !important;
}
.sortable-container > .sortable-item:hover {
    cursor: grab;
}
body > .sortable-item:hover {
    display: block;
    cursor: grabbing;
}
